(function(window, $){

var char_map = {
		//Latin
		'': 'A', '': 'A', '': 'A', '': 'A', '': 'A', '': 'A', '': 'AE', '':
		'C', '': 'E', '': 'E', '': 'E', '': 'E', '': 'I', '': 'I', '': 'I',
		'': 'I', '': 'D', '': 'N', '': 'O', '': 'O', '': 'O', '': 'O', '':
		'O', '': 'O', '': 'O', '': 'U', '': 'U', '': 'U', '': 'U', '': 'U',
		'': 'Y', '': 'TH', '': 'ss', '':'a', '':'a', '': 'a', '': 'a', '':
		'a', '': 'a', '': 'ae', '': 'c', '': 'e', '': 'e', '': 'e', '': 'e',
		'': 'i', '': 'i', '': 'i', '': 'i', '': 'd', '': 'n', '': 'o', '':
		'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'o', '': 'u', '': 'u',
		'': 'u', '': 'u', '': 'u', '': 'y', '': 'th', '': 'y',

		//Greek
		'':'a', '':'b', '':'g', '':'d', '':'e', '':'z', '':'h', '':'8',
		'':'i', '':'k', '':'l', '':'m', '':'n', '':'3', '':'o', '':'p',
		'':'r', '':'s', '':'t', '':'y', '':'f', '':'x', '':'ps', '':'w',
		'':'a', '':'e', '':'i', '':'o', '':'y', '':'h', '':'w', '':'s',
		'':'i', '':'y', '':'y', '':'i',
		'':'A', '':'B', '':'G', '':'D', '':'E', '':'Z', '':'H', '':'8',
		'':'I', '':'K', '':'L', '':'M', '':'N', '':'3', '':'O', '':'P',
		'':'R', '':'S', '':'T', '':'Y', '':'F', '':'X', '':'PS', '':'W',
		'':'A', '':'E', '':'I', '':'O', '':'Y', '':'H', '':'W', '':'I',
		'':'Y',

		//Turkish
		'':'s', '':'S', '':'i', '':'I', '':'c', '':'C', '':'u', '':'U',
		'':'o', '':'O', '':'g', '':'G',

		//Russian
		'':'a', '':'b', '':'v', '':'g', '':'d', '':'e', '':'yo', '':'zh',
		'':'z', '':'i', '':'j', '':'k', '':'l', '':'m', '':'n', '':'o',
		'':'p', '':'r', '':'s', '':'t', '':'u', '':'f', '':'h', '':'c',
		'':'ch', '':'sh', '':'sh', '':'', '':'y', '':'', '':'e', '':'yu',
		'':'ya',
		'':'A', '':'B', '':'V', '':'G', '':'D', '':'E', '':'Yo', '':'Zh',
		'':'Z', '':'I', '':'J', '':'K', '':'L', '':'M', '':'N', '':'O',
		'':'P', '':'R', '':'S', '':'T', '':'U', '':'F', '':'H', '':'C',
		'':'Ch', '':'Sh', '':'Sh', '':'', '':'Y', '':'', '':'E', '':'Yu',
		'':'Ya',

		//Ukranian
		'':'Ye', '':'I', '':'Yi', '':'G', '':'ye', '':'i', '':'yi', '':'g',

		//Czech
		'':'c', '':'d', '':'e', '': 'n', '':'r', '':'s', '':'t', '':'u',
		'':'z', '':'C', '':'D', '':'E', '': 'N', '':'R', '':'S', '':'T',
		'':'U', '':'Z',

		//Polish
		'':'a', '':'c', '':'e', '':'l', '':'n', '':'o', '':'s', '':'z',
		'':'z', '':'A', '':'C', '':'e', '':'L', '':'N', '':'o', '':'S',
		'':'Z', '':'Z',

		//Latvian
		'':'a', '':'c', '':'e', '':'g', '':'i', '':'k', '':'l', '':'n',
		'':'s', '':'u', '':'z', '':'A', '':'C', '':'E', '':'G', '':'i',
		'':'k', '':'L', '':'N', '':'S', '':'u', '':'Z',

		//Lithuanian
		'':'a', '':'c', '':'e', '':'e', '':'i', '':'s', '':'u', '':'u',
		'':'z', '':'A', '':'C', '':'E', '':'E', '':'I', '':'S', '':'U',
		'':'U', '':'Z',

		//Currency
		'': 'euro', '': 'cruzeiro', '': 'french franc', '': 'pound', 
		'': 'lira', '': 'mill', '': 'naira', '': 'peseta', '': 'rupee',
		'': 'won', '': 'new shequel', '': 'dong', '': 'kip', '': 'tugrik',
		'': 'drachma', '': 'penny', '': 'peso', '': 'guarani', '': 'austral',
		'': 'hryvnia', '': 'cedi', '': 'cent', '': 'yen', '': 'yuan',
		'': 'yen', '': 'rial', '': 'ecu', '': 'currency', '': 'baht',

		//Symbols
		'':'(c)', '': 'oe', '': 'OE', '': 'sum', '': '(r)', '': '+',
		'': '"', '': '"', '': "'", '': "'", '': 'd', '': 'f', '': 'tm', 
		'': 'sm', '': '...', '': 'o', '': 'o', '': 'a', '': '*', 
		'': 'delta', '': 'infinity', '': 'love', '&': 'and'
	};

	var Slugify = function(e, cfg)
	{
		this.cfg = cfg || {};
		
		if (typeof this.cfg.slug == 'undefined')
		{
			console.log('Error no slug field');
			return;
		}
		this.type   = this.cfg.type||'_';
		this.$slug  = $(this.cfg.slug);
		this.$title = $(e);
		
		this.register_events();
	};

	Slugify.prototype = {
		encode: function(str)
		{
			if (typeof str != 'undefined')
			{
				var slug = '';
				str = $.trim(str);

				for (var i = 0; i < str.length; i++)
				{
					slug += (char_map[str.charAt(i)]) ? char_map[str.charAt(i)] : str.charAt(i);
				}
				
				return slug.toLowerCase().replace(/-+/g, '').replace(/\s+/g, this.type).replace(/[^a-z0-9_\-]/g, '');
			}
		},
		register_events: function(){
			var me = this, $title = this.$title, $slug = this.$slug;
			
			// Check if the	 field is a text field or undefined (select)
			if ($title.attr('type') == 'text')
			{
				// For text fields
				$title.keyup(function(e) {
					$slug.val(me.encode(e.currentTarget.value));
				});

				// Check if it's empty first and populate if so
				if ($slug.val() == '')
				{
					$slug.val(me.encode($title.val()));
				}
			}
			else
			{
				// For dropdown fields
				if ($title.hasClass('chzn'))
				{
					$title.chosen.change(function(e) {
						$slug.val(me.encode(e.currentTarget.value));
					});
				}
				else
				{
					$title.change(function(e) {
						$slug.val(me.encode(e.currentTarget.value));
					});
				}
				// Check if it's empty first and populate if so
				if ($slug.val() == '')
				{
					$slug.val(me.encode($(':selected', $title).val()));
				}
			}
		}
	};

	$.fn.slugify = function (option) {
		return this.each(function () {
			var $this = $(this)
				, data = $this.data('slugify')
				, options = $.extend({}, $.fn.slugify.defaults, $this.data(), typeof option == 'object' && option);
			if (!data) $this.data('slugify', (data = new Slugify(this, options)));
			if (typeof option == 'string') data[option]();
		})
	};
	$.fn.slugify.defaults = {
		slug: '#slug',
		type:'_',
	};

})(window, jQuery);
